<template>
    <div id="login">
        <div id="regist-box">
            <div id="login-tips">
                <span id="to-login" @click="toLogin">Login</span>
                <span id="to-regist">Regist</span>
                <span id="to-regist-border" ></span>                
            </div>
        <el-form  label-width="55px" :model="formLabelRegist"  :rules="ruleRegist" ref="formRegist">
            <i class="fa fa-envelope-o" aria-hidden="true"></i>
            <el-form-item  prop="email">
                <el-input v-model="formLabelRegist.email" placeholder="Email" ref="formEmail"></el-input>
            </el-form-item>  
            <i class="fa fa-user-circle-o" aria-hidden="true"></i>
            <el-form-item  prop="username" >
                <el-input v-model="formLabelRegist.username" placeholder="Username"></el-input>
            </el-form-item>
            <i class="fa fa-key" aria-hidden="true"></i>
            <el-form-item  prop="password" >
                <el-input v-model="formLabelRegist.password" type="password" autocomplete="off" placeholder="Password"></el-input>
            </el-form-item>
            <i class="fa fa-key" aria-hidden="true"></i>
            <el-form-item  prop="passcheck" >
                <el-input v-model="formLabelRegist.passcheck" type="password" autocomplete="off" placeholder="PassCheck"></el-input>
            </el-form-item><i class="fa fa-codepen" aria-hidden="true"></i>
            <el-form-item  prop="code">
                <el-input v-model="formLabelRegist.code" id="code-Input" placeholder="VerifyCode">
                </el-input>
            </el-form-item>
            <el-button id="send-button" :disabled="disabledCode" @click="sendCode">{{codeButtonText}}</el-button>
            <el-form-item>
                <el-button class="button-handle" @click="handleRegist">Regist</el-button>
            </el-form-item>

        </el-form>
        </div>
    </div>
</template>

<script>
import { warn } from 'vue-class-component/lib/util';
export default {
    data() {
        var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          if (this.formLabelRegist.pswcheck !== '') {
            this.$refs.formRegist.validateField('passcheck');
          }
          callback();
        }
      };
        var valuecheck = (rule, value, callback) => {
            if (value === '') {
            callback(new Error('请再次输入密码'));
            } else if (value !== this.formLabelRegist.password) {
            callback(new Error('两次输入密码不一致!'))
            console.log(value)
            } else {
            callback();
            }
        };
        var emailCheck = (rule, value, callback) => {
            if (value === '') {
            callback(new Error('请输入邮箱'));
            } else {
                this.$axios({
                    url: 'http://39.108.108.227:9527/cd/user/validEmail/' + value,
                    method: 'get'
                }).then(data=>{
                    console.log(data)
                    if(data.data.message == '操作成功') {
                        callback()
                    } else {
                        callback(new Error('该邮箱已注册'));
                    }
                })
            }
        };
        var nameCheck = (rule, value, callback) => {
          if (value === '') {
          callback(new Error('请输入用户名'));
        } else {
            this.$axios({
                url: 'http://39.108.108.227:9527/cd/user/validUsername/' + value,
                method: 'get'
            }).then(data=>{
                if(data.data.message == '操作成功') {
                    callback()
                } else {
                    callback(new Error('该用户名已存在'));
                }
            })
        }
      }
        return{
            formLabelRegist: {
                email: '',
                password: '',
                passcheck: '',
                code: '',
                username: '',
            },
            ruleRegist: {
                email: [{type: 'email',required: true,validator: emailCheck, trigger: 'blur'}],
                username: [{required: true,validator: nameCheck, trigger: 'blur'}],
                password: [{required: true,validator: validatePass, trigger: 'blur'}],
                passcheck: [{required: true,validator: valuecheck, trigger: 'blur'}],
                // code: [{required: true,trigger: 'blur',message:'请输入验证码'}],
            },
            codeImgUrl: 'http://39.108.108.227:9527/cd/user/createCode',
            codeButtonText: 'Send',
            disabledCode: false,
            api: 'http://39.108.108.227:9527' // http://39.108.108.227:9527
        }
    },
    methods: {
        changeCodeImg() {
            var num=Math.ceil(Math.random()*10);//生成一个随机数（防止缓存）
　　　　    this.codeImgUrl='http://39.108.108.227:9527/cd/user/createCode?'+num;
        },
        forgetPassWord() {
            this.$message({
                message: '此功能暂未开放',
                type: 'warning'
            })
        },
        toLogin(){
            this.showLogin = true;
            this.$router.push({path:'/'})
        },
        handleRegist() {
            var formName = 'formEmail'
            this.$refs[formName].validate((valid) => {
                if(valid){
                    this.$axios({
                        url: 'http://39.108.108.227:9527/cd/user/register',
                        method: 'post',
                        data: {
                            'code': this.formLabelRegist.code,
                            'email': this.formLabelRegist.email,
                            'password': this.formLabelRegist.password,
                            'username': this.formLabelRegist.username
                        }
                    }).then(response=>{
                        console.log(response.data.success)
                        if(response.data.success) {
                            this.$message({
                                message: '注册成功',
                                type: 'success'
                            })
                            this.$router.push({path:'/'})
                        } else {
                               this.$message({
                                message: response.data.message,
                                type: 'warning'
                            })
                        }
                    })
                }
            })
        },
        sendCode() {
            var formName = 'formRegist'
            this.$refs[formName].validate((valid) =>{
                if(valid) {
                    this.$axios({
                        url: 'http://39.108.108.227:9527/cd/user/getCode/'+this.formLabelRegist.email,
                        method: 'get'
                    })
                    this.disabledCode = true;
                    let i = 60;
                    this.codeButtonText = i + 's';
                    var that = this;
                    var countdown = setInterval(
                        function() {
                            if(i==0) {
                                that.codeButtonText = 'Send';
                                that.disabledCode = false;
                                clearInterval(countdown);
                            } else {
                                i--;
                                that.codeButtonText = i + 's';
                            }
                        }
                    ,1000)
                }
            })
        },
    }
}
</script>

<style lang="less" scpoed>
    * {
        margin: 0;
        padding: 0;
    }
    #regist-box {
        width: 500px;
        height: 750px;
        border-radius: 10px;
        background-color: #00000080;
        position: absolute;
        top: 50%; left: 80%;
        transform: translate(-50%,-50%);
        // padding-top: 150px;
        #login-tips {
            #to-login {
                display: inline-block;
                font-size: 30px;
                margin: 30px 90px 0 90px ;
                padding: 0 15px 0 15px;
                color: white;
                font-weight: 600;
                cursor: pointer;
            }
            #to-regist {
                display: inline-block;
                font-size: 30px;
                margin: 30px 15px 0 18px;
                padding: 0 15px 0 15px;
                color: white;
                font-weight: 600;
                cursor: pointer;
            }
            #to-regist-border {
                display: inline-block;
                border: 2px solid #18c869;
                border-radius: 2px;
                font-size: 30px;
                margin: 30px 0 0 315px;
                padding: 0 60px 0 60px;
                background-color: #18c869
            }
        }
        .el-form-item__label {
            font-size: 18px; 
            // margin-top: 22px;            
        }
        .el-input__inner {
            width: 350px!important;
            height: 52px;
            border: 0;
            border-bottom: 2px solid white;
            background: #ffffff00;
            border-radius: 0;
            font-weight: 500;
            font-size: 24px;
            color: white;
            // margin-top: 15px;
        }
        i {
            color: white;
            font-size: 24px;
            position: relative;
            top: 48px;
            right: 20px;
            margin-left: 65px;
        }
       .button-handle {
            width: 350px;
            height: 50px;
            font-size: 20px;
            font-weight: 600;
            background: url(../assets/cool-background2.png);
            border: none;
            background-size: cover;
            background-repeat: no-repeat;
            color: white;
        }
        #code-Input {
            display: inline-block;
            width: 235px!important;
            
        }
        .el-input-group--append {
            width: 130px;
            padding: 0!important;
        }
        .el-form-item__error {
            font-size: 18px;
        }
        #send-button {
            background: url(../assets/cool-background.png);
            background-size: cover;
            background-repeat: no-repeat;
            position: relative;
            border: none;
            color: white;
            bottom: 65px;
            left: 320px;
            font-weight: 400;
            font-size: 20px;
        }
    }

</style>