import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const allowClick = true;
const allowHover = true;
const Formername = '';



const fileposition = [0]

const data = [];
const selection = [];


export default new Vuex.Store({
  state: {
    data,fileposition,allowClick,allowHover,Formername,selection
  },
  getters: {
  },
  mutations: {
    addKey(){
      for(var i=0;i<(this.state as any).data.length;i++){
        Vue.set((this.state as any).data[i],'hover',false) ;
        Vue.set((this.state as any).data[i],'edit',false) ;
      }
      
    }
  },
  actions: {
  },
  modules: {
  }
})
