<template>
  <div id="nav">
    <el-menu class="el-menu-demo" mode="horizontal" router>
      <el-menu-item index="/" id="firstnav">网盘</el-menu-item>
      <el-menu-item @click="forgetPassWord">动态</el-menu-item>
      <el-menu-item @click="forgetPassWord">分享</el-menu-item>
      <el-submenu index="3" id="login">
        <template slot="title"><i class="el-icon-s-custom"></i>{{username}}</template>
        <el-menu-item @click="logOut">注销</el-menu-item>
        <el-menu-item index="2-2">更改密码</el-menu-item>
        <el-menu-item index="2-3">选项3</el-menu-item>
      </el-submenu>
      <!-- <el-menu-item index="/login" id="login">
        <i class="el-icon-s-custom"></i>登录
      </el-menu-item> -->
      <el-menu-item id="search" @click="searchHandle">
        <i class="el-icon-search"></i>搜索
      </el-menu-item>
      <div id="input">
        <el-input id="search-input" v-model="searchInput" placeholder="请输入内容">
      </el-input>
      </div>
    </el-menu>
  </div>
</template>

<script>
export default {
  name: "mynav",
  data() {
    return {
      searchInput:"",
      username: "",
      api: 'http://39.108.108.227:9527/'
    }
  },
  methods: {
    forgetPassWord() {
      this.$message({
          message: '此功能暂未开放',
          type: 'warning'
      })
    },
    logOut() {
        this.$confirm('是否退出登录', '确认信息', {
          distinguishCancelAndClose: true,
          confirmButtonText: '确定',
          cancelButtonText: '取消'
          })
          .then(() => {
            this.$axios({
              url: this.api + 'cd/user/logout',
              method: 'get'
            })
            localStorage.removeItem('token');
            this.$router.push('/')
            })
          .catch(action=> {
          });
    },
    getUsername() {
      this.$axios({
        url: this.api + 'cd/user/getUsername',
        method: 'get'
      }).then(data=>{
        this.username = data.data.data;
      })
    },
    async searchHandle(){
      var datalist = [];
      await this.$axios({
        url: this.api + 'cd/search/directory/?key=' + this.searchInput,
        method: 'get',
      }).then(response=>{
        console.log(response.data.data);
        if(response.data.data){
          datalist = response.data.data;
        }
      })
      await this.$axios({
        url: this.api + 'cd/search/files/?key=' + this.searchInput,
        method: 'get'
      }).then(response=>{
        console.log(response.data.data);
        if(response.data.data){
          this.$store.state.data = datalist.concat(response.data.data);
        }
        this.$store.commit('addKey')
      })
    }
  },
  created: function() {
    this.getUsername()
  }
}
</script>

<style lang="less">
* {
  padding: 0;
  margin: 0;
}
#nav {
  #firstnav {
    margin-left: 220px;
  }
  li {
    font-size: 20px;
  }
  .el-menu-item {
    margin-right: 20px;
  }
  #login {
    margin-left: calc(100% - 1200px);;
  }
  #input {
    display: inline-block;
    width: 200px;
    position: relative;
    top: 10px;
  }
  @media screen and (max-width:850px) {
    #login {
      margin-left: 0;
    }
  }
  @media screen and (max-width:710px) {
    #firstnav {
      margin-left: calc(100% - 770px);
    }
  }

}
</style>
