<template>
    <div id="login">
        <div id="login-box">
            <div id="login-tips">
                <span id="to-login" >Login</span>
                <span id="to-regist" @click="toRegist">Regist</span>
                <span id="to-login-border" ></span>
            </div>
        <el-form  label-width="55px" :model="formLabelLogin" :rules="ruleLogin" ref="formLogin">
            <i class="fa fa-user-circle"  aria-hidden="true"></i>
            <el-form-item label="" prop="email">
                <el-input v-model="formLabelLogin.email" placeholder="Email">
                </el-input>
            </el-form-item> 
            <i class="fa fa-key"  aria-hidden="true"></i>
            <el-form-item label="" prop="password">
                <el-input v-model="formLabelLogin.password" type="password" placeholder="Password"></el-input>
            </el-form-item>
            <i class="fa fa-codepen"  aria-hidden="true"></i>
            <el-form-item label="" prop="code">
                <el-input v-model="formLabelLogin.code" placeholder="VerifyCode"></el-input>
            </el-form-item>
            <el-form-item>
                <img :src="this.codeImgUrl" alt="">
                <a id="codeChange" @click="changeCodeImg">看不清？换一个</a>
            </el-form-item>
            <el-form-item>
                <el-button class="button-handle" @click="handleLogin">Log On</el-button>
            </el-form-item>
            <a id="pswForget" @click="forgetPassWord">忘记密码？</a>
        </el-form>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return{
            formLabelLogin: {
                email: '',
                password: '',
                code: '',
            },
            ruleLogin: {
                email: [{type: 'email',required: true, message: '请输入正确的邮箱地址', trigger: 'blur'}],
                password: [{required: true, message: '请输入密码', trigger: 'blur'}],
            },
            codeImgUrl: 'http://39.108.108.227:9527/cd/user/createCode',
            api: 'http://39.108.108.227:9527' // http://39.108.108.227:9527
        }
    },
    methods: {
        changeCodeImg() {
            var num=Math.ceil(Math.random()*10);//生成一个随机数（防止缓存）
　　　　    this.codeImgUrl='http://39.108.108.227:9527/cd/user/createCode?'+num;
        },
        forgetPassWord() {
            this.$message({
                message: '此功能暂未开放',
                type: 'warning'
            })
        },
        toRegist(){
            this.showLogin = false;
            this.$router.push({path:'/regist'})
            // var formName = 'ruleForm'
            // this.$refs[formName].resetFields();
        },
        handleLogin() {
            var formName = 'formLogin'
            this.$refs[formName].validate((valid) => {
                console.log(this.formLabelLogin.code)
                if(valid){
                    this.$axios({
                        url: this.api + '/cd/user/login',
                        method: 'post',
                        data: {
                            'email': this.formLabelLogin.email,
                            'password': this.formLabelLogin.password,
                            'verifyCode': this.formLabelLogin.code
                            }
                    }).then(response=>{
                        console.log(response);
                        if(response.data.success==true) {
                            let obj = {
                                data: response.data.data,
                                time: Date.now(),
                                expire: 21600000
                            }
                            localStorage.setItem("token",JSON.stringify(obj));
                            this.$router.push({path:'/main'})
                        } else {
                            this.$message({
                                message: response.data.message,
                                type: 'warning',
                            })
                            this.changeCodeImg()
                            this.$router.push({path:'/login'})
                        }
                    })
                }
            })
        },
    },
    created: function() {
        this.changeCodeImg()
    }
}
</script>

<style lang="less">
    * {
        margin: 0;
        padding: 0;
    }
    html {
        background: url(../assets/head-img4.jpg);
        background-repeat: no-repeat;
        width:100%;
        height:100%;
        background-size: cover;
    }
    #login-box {
        width: 500px;
        height: 650px;
        border-radius: 10px;
        background-color: #00000080;
        // opacity: 0.4;
        position: absolute;
        top: 50%; left: 80%;
        transform: translate(-50%,-50%);
        // padding-top: 150px;
        #login-tips {
            #to-login {
                display: inline-block;
                font-size: 30px;
                margin: 30px 90px 0 90px ;
                padding: 0 15px 0 15px;
                color: white;
                font-weight: 600;
                cursor: pointer;
            }
            #to-regist {
                display: inline-block;
                font-size: 30px;
                margin: 30px 15px 0 18px;
                padding: 0 15px 0 15px;
                color: white;
                font-weight: 600;
                cursor: pointer;
            }
            #to-login-border {
                display: inline-block;
                border: 2px solid #ea8542;
                border-radius: 2px;
                font-size: 30px;
                margin: 30px 30px 0px 90px;
                padding: 0 55px 0px 55px;
                background-color: #ea8542
            }
        }
        .el-form-item__label {
            font-size: 18px; 
            // margin-top: 22px;            
        }
        .el-input__inner {
            width: 350px!important;
            height: 52px;
            border: 0;
            border-bottom: 2px solid white;
            background: #ffffff00;
            border-radius: 0;
            font-weight: 500;
            font-size: 24px;

            color: white;
            // margin-top: 15px;
        }
        i {
            color: white;
            font-size: 24px;
            position: relative;
            top: 48px;
            right: 20px;
            margin-left: 65px;
        }
       .button-handle {
            width: 350px;
            height: 50px;
            font-size: 20px;
            font-weight: 700;
            border: none;
            color: white;
            background: rgb(98,151,165);
            background: linear-gradient(90deg, rgba(98,151,165,1) 0%, rgba(253,29,64,1) 100%);
        }
        img {
            position: relative;
            width: 95px;
            height: 25px;
            left: 40px;
        }
        #codeChange {
            position: relative;
            left: 100px;
            color: #1CB5E0;
            cursor: pointer;
            font-weight: 300;
            font-size: 18px;
        }
        #pswForget {
            margin-left: 350px;
            cursor: pointer;
            color: white
        }
        .el-form-item__error {
            font-size: 18px;
        }
    }

</style>