<template>
    <el-table :data="this.$store.state.data" fit show-header empty-text="暂无文件" style="width: 100%" max-height="830px" 
    @cell-click="showrow"
    @cell-mouse-enter="MouseEnter"
    @cell-mouse-leave="MouseLeave"
    @select="SelectHandle"
    @select-all="SelectAll"
    >
      <el-table-column type="selection" width="30" class="checkbox" min-width="0.5">
      </el-table-column>
      <el-table-column prop="name" label="文件名" min-width="4.5">
        <template slot-scope="scope">
        <i class="fa fa-folder fa-2x" aria-hidden="true" style="color: #FFD659" v-if="scope.row.type==undefined&&!scope.row.edit"></i>
        <i class="fa fa-file-image-o fa-2x" aria-hidden="true" style="color: #FF7743" v-if="scope.row.type=='image'&&!scope.row.edit"></i>
        <i class="fa fa-file-word-o fa-2x" aria-hidden="true" style="color: #4D97FF" v-if="scope.row.type=='document'&&!scope.row.edit"></i>
        <i class="fa fa-file-video-o fa-2x" aria-hidden="true" style="color: #8183F1" v-if="scope.row.type=='video'&&!scope.row.edit"></i> 
        <i class="fa fa-file-archive-o fa-2x" aria-hidden="true" style="color: #8183F1" v-if="scope.row.type=='other'&&!scope.row.edit"></i> 
        <template v-if="scope.row.edit">
          <el-input class="edit-input" size="small" v-model="scope.row.name"></el-input>
        </template>
        <span id="filename" v-else>{{ scope.row.name }}</span>

      </template>
      </el-table-column>
      <el-table-column min-width="2">
      <template slot-scope="scope" >
        <el-button v-show="scope.row.edit" type="success" circle @click="handleSave(scope.$index, scope.row)" size="small" icon="el-icon-check"></el-button>
        <el-button v-show="scope.row.edit" type="danger" circle @click="handleCancel(scope.$index, scope.row)" size="small" icon="el-icon-close"></el-button>
        <el-button type="primary" circle v-show="!scope.row.edit&&scope.row.hover" @click="handleEdit(scope.$index, scope.row)" size="small" icon="el-icon-edit"></el-button>
        <el-button type="primary" circle v-show="!scope.row.edit&&scope.row.hover" @click="handleDelete(scope.$index, scope.row)" size="small" icon="el-icon-delete"></el-button>
        <el-button type="primary" circle v-show="!scope.row.edit&&scope.row.hover" @click="handleShare(scope.$index, scope.row)" size="small" icon="el-icon-share"></el-button>
      </template>
    </el-table-column>
      <el-table-column prop="size" label="大小" min-width="1">
      </el-table-column >
      <el-table-column prop="createtime" label="修改时间" min-width="2">
      </el-table-column>
    </el-table>
</template>

<script>

export default {
    data() {
        return {
          show: false,
          editClick: false,
          api: 'http://39.108.108.227:9527/' //http://192.168.0.235:9527, http://39.108.108.227:9527/
        }
      },
    props: {
      getFile: Function,
    },
    methods: {
      showrow(row,column) {
        if(column.label=='文件名'&&this.$store.state.allowClick==true){
          if(row.type == undefined){
          this.getFile(row.id)
          this.$store.state.fileposition.push(row.id);
          console.log('发送请求');
        }
        console.log(row.type);
        }
      },
      MouseEnter(row) {
        if(this.$store.state.allowHover==true){
          row.hover = true;
        }
      },
      MouseLeave(row) {
        row.hover = false;
      },
      SelectHandle(selection, row) {
        this.$store.state.selection = selection;
        console.log(this.$store.state.selection)
      },
      SelectAll(selection){
        this.$store.state.selection = selection;
        console.log(this.$store.state.selection)
      },
      handleEdit(index,row){
        this.$store.state.Formername = row.name;
        this.editClick = true;
        row.edit = !row.edit;
        this.$store.state.allowClick = false;
        this.$store.state.allowHover = false;
      },
      handleDelete(index,row){
        var Fileurl = this.api + 'cd/file/delete/'
        var Directoryurl = this.api + 'cd/directory/delete/'
        this.$confirm('此操作将永久删除文件，是否继续', '确认信息', {
        distinguishCancelAndClose: true,
        confirmButtonText: '确定',
        cancelButtonText: '取消'
        })
        .then(() => {
          if(row.type==undefined)
          this.deleteFile(Directoryurl,row.id)
          else this.deleteFile(Fileurl,row.id)
          })
        .catch(action=> {
          this.$message({
            type: 'info',
            message: action === 'cancel'
              ? '取消删除'
              : '返回'
          })
        });
      },
      handleShare(index,row){
        this.$axios({
          url: this.api + 'cd/share/createUrl',
          method: 'post',
          data: {
            'fileIdList': [row.id],
            'day': 7,
          }
        }).then(response=>{
          var data = response.data.data;
          this.$confirm(
          '分享链接：'+data,'确认信息', {
        distinguishCancelAndClose: true,
        confirmButtonText: '复制链接',
        cancelButtonText: '关闭'
        })
        .then(action=> {
          this.$message({
            type: 'info',
            message: action 
          })
        });
        })
      },
      handleSave(index,row){
        if(this.editClick) {
          if(row.name==''){
            row.name = this.$store.state.Formername;
            this.$message({
              message: '文件名不能为空',
              type: 'success'
          })
          }
          else {
            this.renameFile(row.id,row.name,row.type,row.parent_id,row.directory_id)              
            this.$message({
            message: '文件名已经成功修改',
            type: 'success'
            })
          }
        }
        else {
          this.createFile(row.name)
        }
        row.edit = false;
        this.editClick = false;
        this.$store.state.allowClick = true;
        this.$store.state.allowHover = true;
      },
      handleCancel(index,row){
        row.edit = false;
        this.$store.state.allowClick = true;
        this.$store.state.allowHover = true;        
        if(this.editClick) {
          row.name = this.$store.state.Formername;
          this.$message({
            message: '重命名已取消',
            type: 'success'
          })
          this.editClick = false;
        }
        else {
          this.$store.state.data.pop();
        }
      },
      createFile(name){
        console.log(name)
        this.$axios({
            url: this.api + 'cd/directory/create',
            method: 'post',
            data: {
              'name': name,
              'parent_id': [...this.$store.state.fileposition].pop()
            }
          }).then(response=>{
            this.getFile([...this.$store.state.fileposition].pop())
          })
      },
      deleteFile(url,id){
        this.$axios({
          url: url+id,
          methods: 'get',
        }).then(response=>{
          this.$message({
            message: response.data.message,
            type: response.data.success
            })
            this.getFile([...this.$store.state.fileposition].pop())
          })
      },
      renameFile(id,name,type,parent_id,directory_id){
        const fileUrl = this.api + 'cd/file/update'
        const folderUrl = this.api + 'cd/directory/update'
        if(type==undefined){
          this.$axios({
            url: folderUrl,
            method: 'post',
            data: {
              'id': id,
              'name': name,
              'parent_id': parent_id
            }
          }).then(res=>{
            console.log(res)
          })          
        }
        else {
          this.$axios({
            url: fileUrl,
            method: 'post',
            data: {
              'id': id,
              'name': name,
              'directory_id': directory_id
            }
          }).then(res=>{
            console.log(res)
          })          
        }
      }
    }   
}
</script>

<style lang="less">
 #filename {
     position: relative;
     bottom: 4px;
     margin-left: 15px;
     cursor: pointer;
 }
.fa-share-alt:before {
  margin-left: 150px;
}
.el-table .cell {
  text-overflow: clip!important
}
.el-checkbox__inner {
  border: 1px solid #DCDFE6!important /* no */
}
</style>