<template>
  <div id="main">
    <div id="mynav">
    <mynav/>
  </div>
    <el-container style="border: 1px solid #eee;background: white">
    <!--                    aside                    -->
  <el-aside width="18vh" >
    <el-menu
      background-color="rgb(238, 241, 246)"
      default-active="2"
      class="el-menu-vertical-demo"
    >
      <el-menu-item @click="getFile(0)">
        <i class="el-icon-menu"></i>
        <span slot="title">全部文件</span>
      </el-menu-item>
      <el-menu-item @click="getByType('img')">
        <i class="el-icon-picture-outline"></i>
        <span slot="title">图片</span>
      </el-menu-item>
      <el-menu-item @click="getByType('document')">
        <i class="el-icon-document"></i>
        <span slot="title">文档</span>
      </el-menu-item>
      <el-menu-item @click="getByType('video')">
        <i class="el-icon-setting"></i>
        <span slot="title">视频</span>
      </el-menu-item>
      <!-- <el-menu-item @click="getByType()">
        <i class="el-icon-headset"></i>
        <span slot="title">音乐</span>
      </el-menu-item> -->
      <el-menu-item @click="getDelete">
        <i class="el-icon-headset"></i>
        <span slot="title">回收站</span>
      </el-menu-item>
    </el-menu>
    <div id="progress">
      <el-progress :percentage="this.percentage" ></el-progress>
      <p>{{this.percentageNow}}/{{this.percentageMax}}</p>
    </div>
  </el-aside>
  <!--                    header                    -->
  <el-container style="height: 93vh;">
    <el-header style="height: 80px">
      <el-upload action="http://39.108.108.227:9527/cd/file/upload" :headers="headers" multiple  :on-progress="showProgress" :on-success="reload" :show-file-list="false" :data="{'directory_id': [...this.$store.state.fileposition].pop()}" style="display: inline-block">
        <el-button type="primary" style="margin:10px 10px 0 0"><i class="fa fa-upload fa-lg" style="margin-right:10px"></i>上传</el-button>
      </el-upload>
      <button class="primary-button" @click="AddFolder"><i class="fa fa-plus fa-lg" style="margin-right:10px"></i>新建文件夹</button>
      <button class="primary-button" @click="handleDownload()"><i class="fa fa-download fa-lg" style="margin-right:10px"></i>下载</button>
      <div id="file-position">
        <span id="return" @click="pageReturn()" v-if="this.$store.state.fileposition[this.$store.state.fileposition.length-1]!='0'">返回上一级</span>
        <span v-if="this.$store.state.fileposition[this.$store.state.fileposition.length-1]!='0'">|</span> 
        <span @click="pageReturnAll()" >全部文件</span>
      </div>
    </el-header>
  <!--                    main                    -->  
    <el-main>
      <mainrow :getFile = this.getFile />
      <!-- <mainrowtest/> -->
    </el-main>
  </el-container>
</el-container>
 <div id="uploadList" v-if="this.listShow">
    <div id="listHead">
      <span @click="showList">上传列表</span>
      <a @click="showList">
        <i class="fa fa-minus "></i>
      </a>
      <a @click="listShow = !listShow">
        <i class="fa fa-times "></i>
      </a>
    </div>
    <div id="listBody">
      <el-table :data="this.uploadfiles" fit show-header empty-text="暂无文件">
        <el-table-column prop="name" label="文件名">
        </el-table-column>
        <el-table-column label="状态">
          <template slot-scope="scope">
            <el-progress :percentage="Math.floor(scope.row.percentage)" v-if="getStatus(scope.row.percentage)"></el-progress>
            <i class="fa fa-check-circle fa-2x" v-else style="color: #5CD992"></i>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
  </div>
    
</template>

<script>
import mainrow from '../components/main-row'
import mynav from '../components/MyNav'
export default {
  data() {
    var user_token = JSON.parse(localStorage.getItem('token'))
    return {
      uploadfiles: [],
      listShow: false,
      listHide: false,
      api: 'http://39.108.108.227:9527/',
      headers: {
        token: user_token.data
      },
      percentageMax: 1,
      percentageNow: 0,
      percentage: 0,
    }
  },
  components: {
    mainrow,mynav
  },
  methods: {
    pageReturn() {
      this.$store.state.fileposition.pop();
      var lastarr = this.$store.state.fileposition[this.$store.state.fileposition.length-1]
      this.getFile(lastarr)
    },
    pageReturnAll() {
      this.$store.state.fileposition = [0];
      this.getFile(0)
    },
    AddFolder(){
      if(this.$store.state.allowClick){
        this.$store.state.data.push({
          name: '新建文件夹',
          size: '-',
          date: this.getDate(),
          edit: true,
          hover: false,
        })
        this.$store.state.Formername = '新建文件夹';
        this.$store.state.allowClick = false;
        this.$store.state.allowHover = false;
      }
    },
    getDate(){
      var date = new Date();
      var year = date.getFullYear();
      var month = date.getMonth()+1;
      var day = date.getDate();
      if(month<=9) month = '0'+month;
      if(day<=9) day = '0'+day;
      var nowday = year + '-'  + month + '-'  + day;
      return nowday;
    },
    showProgress(event, file, fileList){           /*       BUG           */ 
      console.log(file.percentage)
      this.uploadfiles = fileList;       
      this.listShow = true;
      if(file.percentage==100)
      this.getFile([...this.$store.state.fileposition].pop())
    },
    async getFile(id){
      var datalist = [];
      await this.$axios({
        url: this.api + 'cd/directory/getFiles/'+id,
        method: 'get',
      }).then(response=>{
        if(response.data.data){
          datalist = response.data.data;
        }
      })
      await this.$axios({
        url: this.api + 'cd/file/getFiles/'+id,
        method: 'get',
      }).then(response=>{
        if(response.data.data){
          this.$store.state.data = datalist.concat(response.data.data);
        }
        this.$store.commit('addKey')
      })
    },
    handleDownload(){
      var selections = this.$store.state.selection;
      console.log(selections)
      if(selections==[]){
        return;
      }
      else {
        for (const index in selections) {
          console.log(selections[index].name)
          this.downloadFile(selections[index].id,selections[index].name)
        }
      }
    },
    downloadFile(id,fileName){
      const ele = document.createElement('a');
      const url = this.api + 'cd/file/download/'+id
      ele.setAttribute('href',url); //设置下载文件的url地址
      ele.setAttribute('download' , fileName);//用于设置下载文件的文件名
      ele.click();
    },
    reload(response, file, fileList){
      this.getFile([...this.$store.state.fileposition].pop())
      this.getPercent();
    },
    async getPercent(){
      await this.$axios({
          url: this.api + 'cd/file/getAllSpace',
          method: 'get',
        }).then(response=>{
          this.percentageMax = response.data.data;
          console.log(this.percentageMax)
        })
      await this.$axios({
          url: this.api + 'cd/file/getUsedSpace',
          method: 'get',
        }).then(response=>{
          this.percentageNow = response.data.data;
          console.log(this.percentageNow)
        })
        this.percentage = Math.ceil(this.percentageNow/this.percentageMax*100)
        this.percentageMax = this.percentageMax/1024/1024 + 'M' 
        this.percentageNow = Math.ceil(this.percentageNow/1024)+ 'K'
    },
    getStatus(percentage){
        return !(percentage==100);
      },
    showList(){
      var list = document.getElementById('uploadList');
      this.listHide = !this.listHide;
      if(this.listHide){
        list.style.height = "65px";
      }
      else {
        list.style.height = "5.333333rem";
      }
    },
    getDelete(){
      var datalist = [];
      this.$axios({
        url: this.api + 'cd/directory/getDelete',
        method: 'get',
      }).then(response=>{
        if(response.data.data){
          datalist = response.data.data;
        }
        this.$axios({
          url: this.api + 'cd/file/getDelete',
          method: 'get',
        }).then(response=>{
           if(response.data.data){
              this.$store.state.data = datalist.concat(response.data.data);
              this.$store.commit('addKey')
            }
        })
      })
    },
    getByType(type) {
      this.$axios({
          url: this.api + 'cd/file/getByType' ,
          method: 'get',
          params: {
            type: type
          }
        }).then(response=>{
          this.$store.state.data = response.data.data;
          this.$store.commit('addKey')
        })
    },
    
  },
  created: function(){
    this.getFile(0);
    this.getPercent();
  }
}
</script>

<style lang="less">
  .el-aside {
    background-color: rgb(238, 241, 246);
  }
  #progress {
    .el-progress-bar__outer {
    background-color: white;
  }
  p {
    text-align: center;
  }
  }
  .el-icon-folder-add:before{
    width: 20px;
  }
  .el-header {
    .el-button {
      border: 1px solid #DCDFE6;/* no */
    }
    .primary-button {
    display: inline-block;
    color: #409EFF;
    font-size: 14px;
    background-color: white;
    border-color: #409EFF;
    border: 1px solid #DCDFE6;/* no */
    padding: 12px 20px;
    line-height: 1;
    cursor: pointer;
    border-radius: 4px;
    outline: 0;
    margin:10px 10px 0 0;
    }
    .primary-button:hover{
       color: #0098EA;
    }
  } 
  .el-main {
    height: 620px;
  }
  #file-position {
    span{
      display: inline-block;
      margin: 20px 10px 0 0 ;
      cursor: pointer;
    }
    #return {
      color: #409EFF;
    }
  }
  #uploadList {
    position: fixed;
    right: 20px;bottom: 0px;
    width: 600px;
    height: 400px;
    border: 1px solid #e2e2e2;
    border-radius: 7px ;
    box-shadow: 0 0 10px #ccc;
    z-index: 999;
    background-color: white;
    transition:height 1s;

    #listHead {
      background-color: #F1F3F4;
      height: 50px;
      span {
        display: inline-block;
        width: 500px;
        font-weight: 500;
        // top: 50%;
        // transform: translate(1%,50%);
        color: #515851;
        padding: 14px 0 14px 7px;
      }
      a {
        display: inline-block;
        color: #515851;
        margin: 0 10px;
      }
    }
  }
</style>